body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h5 {
  font-family: "Montserrat", sans-serif;
}

.Home .lander {
    padding: 10px 0;
    text-align: center;
}

.Home .lander h1 {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    color: deepskyblue;
}

.Home .lander p {
    color: aqua;
}

.Home .toolbar {
    float: right;
    padding-right: 20px;
}

.Home .projects-list {
    padding-top: 50px;
}
.Login {
    padding: 100px;
    text-align: left;
    min-width: 500px;
}

.Login .alert {
    margin-top: 20px;
    padding-top: 0px;
}
.LoadingButton .spinning.glyphicon {
    padding-left: 5px;
    -webkit-animation: spin 1s infinite linear;
            animation: spin 1s infinite linear;
}
@-webkit-keyframes spin {
    from {-webkit-transform: scale(0.9) rotate(0deg);transform: scale(0.9) rotate(0deg)}
    to {-webkit-transform: scale(1) rotate(360deg);transform: scale(1) rotate(360deg)}
}
@keyframes spin {
    from {-webkit-transform: scale(0.9) rotate(0deg);transform: scale(0.9) rotate(0deg)}
    to {-webkit-transform: scale(1) rotate(360deg);transform: scale(1) rotate(360deg)}
}
.Register {
    padding: 100px;
    text-align: left;
}

.ProjectSettings {
    overflow-y: auto;
}

.delete-prompt {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

table, thead, td {
    text-align: left;
}
.toolbar {
    float: right;
    padding-right: 50px;
}
.NotFound {
    padding-top: 160px;
    text-align: center;
}
.App {
    text-align: center;
}

.App .navbar {
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: aquamarine;
}

.App .navbar-brand {
    font-weight: bold;
    padding: 0px 10px 10px 20px;
    text-align: left;
}
